body {
    background-color: #000;
    color: #fff;
}

.app {
    text-align: center;
    padding: 5%;
}

.app-title {
    font-size: 26px;
}

.profile {
    display: flex;
    justify-content: center;
}

.profile-img {
    width: 200px;
    height: 200px;
    border: 3px solid #fff;
    border-radius: 50%;
    object-fit: cover;
}